import React, { useContext } from 'react';
import { Dropdown } from 'semantic-ui-react';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'src/@store';
import { Mix } from 'src/models/mix';
import { AuthContextProps, AuthContext } from 'src/providers/AuthProvider';
import { download, pdf } from 'src/utils/drive';
import { addToUserFavourites, removeFromUserFavourites } from 'src/@creators/favorites';

type Props = {
  mix: Mix;
};

const Actions = ({ mix: { id = '', sourceId, trackListId } }: Props) => {
  const {
    auth: { user },
    userFavourites: { ids = [] },
  } = useSelector<RootState, RootState>((state) => state);
  const { openLoginModal } = useContext(AuthContext) as AuthContextProps;
  const has = user && ids.includes(id);
  const dispatch = useDispatch();

  return (
    <div className="actions-wrapper">
      <Dropdown text="..." floating direction="left">
        <Dropdown.Menu>
          {user && (
            <Dropdown.Item
              icon="heart"
              onClick={
                has
                  ? () => dispatch(removeFromUserFavourites(user.uid, id))
                  : () => dispatch(addToUserFavourites(user.uid, id))
              }
              text={has ? 'Remove from favorites' : 'Add to favorites'}
            />
          )}
          {!user && (
            <Dropdown.Item
              icon="heart"
              text="Add to favorites"
              onClick={() => openLoginModal()}
            />
          )}
          <Dropdown.Item
            as="a"
            icon="download"
            text="Download"
            href={download(sourceId)}
          />
          {trackListId && (
            <Dropdown.Item
              as="a"
              icon="numbered list"
              text="Show track list"
              href={pdf(trackListId)}
            />
          )}
        </Dropdown.Menu>
      </Dropdown>
    </div>
  );
};

export default Actions;
