import React from 'react';
import { Dropdown, Icon } from 'semantic-ui-react';

type Props = {
  extra: {
    actionName?: string;
  };
};

const Actions = ({ extra }: Props) => {
  const { actionName } = extra;
  return (
    <div className="player-actions">
      {!actionName && (
        <Dropdown item direction="left" icon="bars">
          <Dropdown.Menu>
            <Dropdown.Item>Electronics</Dropdown.Item>
            <Dropdown.Item>Automotive</Dropdown.Item>
            <Dropdown.Item>Home</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      )}
      {actionName === 'go-to-detail' && <Icon name="angle up" link size="large" />}
    </div>
  );
};

export default Actions;
